#__particles {
  position: absolute;
  width: 100%;
  height: 100%;
  z-index: 1;
}

.wave-section {
  overflow: hidden;
}

.partical-bg-wrap {
  position: fixed;
  top: 0;
  left: auto;
  right: auto;
  width: 100%;
  height: 100%;
  overflow: hidden;
  z-index: -1;
}

.custom-table td,
.custom-table th {
  border: 1px solid #707070 !important;
}

.ml-4rem {
  margin-left: 4rem !important;
}

.panel-fifth h3 {
  font-size: 2.2rem;
  line-height: 1.4;
}

@media screen and (max-width: 767px) {
  .panel-fifth {
    margin-top: 6rem !important;
  }

  .panel-fifth .subscribe-section {
    margin-top: 6rem !important;
  }
  .panel-fifth .subscribe-section .primary-button {
    margin: 15px auto 0;
  }
}
.mb-4rem {
  margin-bottom: 4rem !important;
}

.subscribe-section-break1 {
  display: inline;
}

.subscribe-section {
  margin: 0px 10vw;
}

@media screen and (max-width: 360px) {
  .subscribe-section-break1 {
    display: none;
  }

  .subscribe-section-break2 {
    display: inline;
  }
}
@media screen and (max-width: 767px) {
  h4 {
    font-size: 1rem !important;
  }

  .subscribe-section {
    margin: 0px 0.5vw !important;
    text-align: center !important;
  }
  .subscribe-section h3 {
    font-size: 1.5rem !important;
    text-align: center !important;
  }
  .subscribe-section button {
    margin-top: 50px !important;
  }
}
